/* ------------small device-------------- */

@media screen and (max-width: 600px){
   
    section > h2 {
        margin-top: 2rem;;
    }

    section{
        height: auto;
    }

    .about-me{
        width: 70%;
        margin: 3rem;
    }

    .about-cards{
        align-items: center;
        flex-direction: column;
        justify-content: space-between;
        gap: 1rem;
        /* margin:1rem 0; */
        width: 100%;
    }

    .about-card{
        margin:1rem;
        width: 100%;
    }

    .about-content{
        text-align: center;
    }


    .about-content p{
        margin:1.5rem 0;
    }


  
}